<template>
  <div>
    <div>
      <!-- Navbar -->
      <b-navbar variant="white" class="border-bottom p-3">
        <b-container fluid class="px-lg-7 mx-lg-5">
          <div class="navbar-brand pb-0 ml-3">
            <!-- Logo -->
            <a class="router-link-active" href="https://ninjacapture.com/">
              <div>
                <img src="../../assets/ninjacapture/logo.png" alt="Ninjacapture" class="ninja-capture-logo" />
              </div>
            </a>
          </div>
          <div class="ml-auto mr-4">
            <a href="https://500apps.com/" target="_blank" class="btn btn-primary btn-sm">Get NinjaCapture for free</a>
          </div>
        </b-container>
      </b-navbar>
      <template v-if="!isDataLoaded"><b-spinner class="d-flex mx-auto mt-8" variant="primary" label="Loading..."></b-spinner></template>
      <template v-else>
        <b-container fluid class="px-lg-6 image-details-page">
          <div class="justify-content-center m-0 mt-4 mt-lg-5 row">
            <div class="lhs-width bg-white border-bottom border-left border-right border-top overflow-auto p-4  position-relative rounded h-100">
              <h2 class="mb-0 my-auto text-left">{{ fileData.name }}</h2>
              <!-- Description -->
              <p class="border-bottom font-size-sm mb-md-4 mb-3 pb-3 text-muted text-left">{{ fileData.description }}</p>
              <!-- Image -->
              <div v-if="fileType.length > 0 && fileType == 'image'" class="img-wrapper">
                <img class="rounded  img-rounded img-fluid w-100" :src="fileData.url" />
              </div>
              <!-- Video -->
              <div v-else-if="fileType.length > 0 && fileType == 'video'">
                <video class="border rounded img-rounded img-fluid col-12 px-0" :src="fileData.url" controls autoplay></video>
              </div>
              <!-- Audio -->
              <audio v-else-if="fileType.length > 0 && fileType == 'audio'" class="mt-6 d-flex mx-auto  rounded" :src="fileData.url" controls autoplay></audio>
              <iframe v-else class="border-0 h-100 mx-3" :src="fileData.url"></iframe>
            </div>
            <!-- Cloud Apps -->
            <div class="rhs-width">
              <b-card>
                <div class="d-flex justify-content-between align-items-center">
                  <h3 class="mb-0">Save</h3>
                  <!-- we will do later for download -->
                  <div class="d-none">
                    <a class="d-flex justify-content-start align-items-center cursor-pointer" :href="fileData.url" :download="fileData.name + '.' + extension[fileType]">
                      <div class="d-flex">
                        <div>
                          <button class="btn btn-outline-secondary btn-sm" style="line-height: 1.5;">
                            <span class="fe fe-airplay position-relative pr-1" style="top: 2px;"></span
                            ><span>
                              Download
                            </span>
                          </button>
                        </div>
                      </div>
                    </a>
                  </div>
                </div>
                <hr />
                <p class="text-danger" v-if="Object.keys(cloudErrorMessage)">{{ cloudErrorMessage.error }}</p>
                <b-row class="px-1">
                  <b-col lg="4" class="px-2" v-for="(app, index) in cloudApps" :key="index" ref="apps">
                    <b-card class="mb-2 shadow-sm" body-class="p-3">
                      <div class="text-center" v-if="index < cloudApps.length - 1" @click="saveToCloud(app)" :class="isCloudUploading[app] ? '' : 'cursor'">
                        <img :src="getLogo(app)" :alt="app" class="avatar-xs rounded-circle" />
                        <p class="mt-2 pt-1 app-hover">{{ app }}</p>
                        <b-spinner v-if="isCloudUploading[app]" class="mt-1 cloud-spiner ml-2" variant="primary" label="Loading..." small></b-spinner>
                      </div>
                      <div class="text-center" v-else @click="saveToCloud(app)" :class="isCloudUploading[app] ? '' : 'cursor'">
                        <img :src="getLogo(app)" :alt="app" class="avatar-xs rounded-circle" />
                        <p class="mt-2 pt-1 app-hover">{{ app }}</p>
                        <b-spinner v-if="isCloudUploading[app]" class="mt-1 cloud-spiner ml-2" variant="primary" label="Loading..." small></b-spinner>
                      </div>
                    </b-card>
                  </b-col>

                  <b-col lg="12" class="px-2" v-for="app in cloudApps" :key="app">
                    <!-- Cloud App URL -->
                    <b-form-group v-if="cloudURL[app]" class="mb-0 mt-3">
                      <label>{{ app }}</label>
                      <b-input-group class="mb-0 mr-sm-2 mb-sm-0 border rounded col px-0">
                        <b-form-input aria-describedby="input-live-help input-live-feedback" class="border-left-0 border-top-0 border-bottom-0" :value="cloudURL[app]" trim disabled></b-form-input>
                        <b-button variant="bg-light" class="rounded-left-0 copy-icon" @click="copyLink(cloudURL[app], app)">
                          <span v-if="isCopied[app]" class="fe fe-check-circle text-success" v-b-tooltip.hover title="Copied"></span>
                          <span v-else class="fe fe-clipboard text-primary" v-b-tooltip.hover title="Copy"></span>
                        </b-button>
                      </b-input-group>
                    </b-form-group>
                  </b-col>
                  <!-- Alert message -->
                  <b-col lg="12">
                    <b-alert v-model="toaster" class="showToster alert alert-warning mb-0 small mt-3 rounded p-3" variant="warning" dismissible
                      ><span class="text-dark align-bottom">Save is in progress, please wait until it completes.</span>
                    </b-alert>
                  </b-col>
                </b-row>
              </b-card>
              <b-card>
                <h3>Comments</h3>
                <hr />
                <!-- Title, Description & Comments -->
                <div class="comment-container">
                  <div class="h-100 overflow-auto">
                    <div class="col-md-12">
                      <div v-if="comments.length > 0">
                        <div class="comment-box">
                          <div class="d-flex mb-3 pb-3" v-for="comment in comments" :key="comment.id">
                            <!-- Profile Image -->
                            <div class="mr-2">
                              <div class="vue-avatar--wrapper profile-pic text-capitalize">
                                <small>{{ comment.name[0] }}</small>
                              </div>
                            </div>
                            <div class="w-100">
                              <div class="ml-1 mt-n1">
                                <!-- Profile Name -->
                                <div class="d-flex justify-content-between">
                                  <b class="font-size-sm">{{ comment.name }}</b>
                                  <span class="text-muted ml-2" v-if="comment.comment_at.length > 0">at {{ comment.comment_at }}</span>
                                  <!-- Profile Created Date -->
                                  <p class="text-muted mb-0 small">{{ comment.created_date.substring(6, 16) }}</p>
                                </div>
                                <!-- Comment -->
                                <p class="font-size-sm mb-0 pt-2 text-body word-break">{{ comment.comment }}</p>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div v-else class="align-items-center d-flex h-100 justify-content-center">
                        <div class="text-center mb-3 pb-5 pt-5 text-center">
                          <b-img src="https://infinity.500apps.com/img/empty-states/projectsly/no-discussions.svg" class="img-fluid mb-3" />
                          <h3>No Comments Yet</h3>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="row">
                    <!-- Name -->
                    <div class="border-top col-12 pt-3">
                      <input placeholder="Your name" class="form-control form-control-sm small" v-model="name" />
                      <small class="text-danger" v-if="isError.name && this.name.length == 0">Name is required</small>
                    </div>
                    <!-- Comment -->
                    <div class="col-12 mt-3">
                      <textarea placeholder="Leave a comment" class="form-control small" rows="2" v-model="comment"></textarea>
                      <small class="text-danger" v-if="isError.comment && this.comment.length == 0">Comment is required</small>
                    </div>
                    <!-- Buttons -->
                    <div class="col-12 mt-3 text-right">
                      <button class="btn btn-secondary btn-sm mr-2" @click="hideComment()">Cancel</button>
                      <button class="btn btn-primary btn-sm" @click="addComment()">Comment</button>
                    </div>
                  </div>
                </div>
              </b-card>
            </div>
          </div>
        </b-container>
      </template>
      <b-modal ref="modal" hide-header hide-footer no-close-on-esc no-close-on-backdrop centered size="sm">
        <div class="d-flex" v-if="fileData.share_url_type == 'private'">
          <div class="my-4 mx-auto">
            <p class="mb-0 font-weight-bold">This {{ fileType }} is set to private. Ask the author to change the settings</p>
          </div>
        </div>
        <div class="my-4" v-else>
          <h3>
            <b>This {{ fileType }} is password protected</b>
          </h3>
          <label>Enter Password(sent in mail)*:</label>
          <input type="password" class="form-control border-right-0 border-left-0 border-top-0 rounded-0" v-model="password" />
          <p class="text-danger mb-0" v-if="errorMessage.length > 0">{{ errorMessage }}</p>
          <b-button variant="primary" size="sm" class="float-right px-4 my-3" @click="checkPassword()">Enter</b-button>
        </div>
      </b-modal>
    </div>
  </div>
</template>
<script>
  import NinjaCapture from "@/js/ninjacapture/ninjacapture";
  import copyToClipboard from "@/js/core/copy-to-clipboard";
  import Upload from "@/js/ninjacapture/upload";
  import { decompressFromEncodedURIComponent as lzStringDecompress } from "lz-string";
  export default {
    data() {
      return {
        fileData: {},
        isCopied: {},
        isCloudUploading: {},
        cloudURL: {},
        cloudApps: ["Google Drive", "Dropbox", "Box"],
        extension: {
          image: "png",
          video: "webm",
          audio: "mp3",
        },
        showComment: false,
        comment: "",
        name: "",
        comments: [],
        isError: {
          name: false,
          comment: false,
        },
        userInfo: null,
        password: "",
        errorMessage: "",
        isDataLoaded: false,
        id: null,
        fileType: "",
        toaster: false,
        cloudErrorMessage: "",
      };
    },
    mounted() {
      // Decompress ninjacapture token ,which is from location path
      [this.userInfo, this.fileType, this.id] = window.location.href.includes("/nc")
        ? lzStringDecompress(location.pathname.split("/")[2]).split("/")
        : lzStringDecompress(location.pathname.split("/")[1]).split("/");
      console.log(this.userInfo, this.fileType, this.id);
      Upload.initJS();
      this.getFileData();
      this.getComments();
    },
    methods: {
      /**
       * Get File response
       */
      getFileData() {
        NinjaCapture.get(this.id).then(async (response) => {
          this.fileData = response;
          /**
           * Get file data from cloud
           * @param {String} URL cloud URL
           */
          if (window.location.href.includes("/nc") && this.fileType == "image") {
            var binaryResponse = await NinjaCapture.getExternalURL(this.fileData.url);
            this.fileData.url = binaryResponse.data;
          }
          // Show Modal if type is private or password protected
          if (response.share_url_type === "public") this.isDataLoaded = true;
          else this.$refs.modal.show();
          // Show Created Date in Date Month, year Format
          // Ex: 11 Jun, 2021
          this.fileData.created_date = this.fileData.created_date.substring(5, 11) + "," + this.fileData.created_date.substring(11, 16);
        });
      },

      /**
       * Copy URL
       */
      copyLink(url, type) {
        let response = copyToClipboard.copy(url);
        if (response) {
          if (!this.isCopied[type]) this.$set(this.isCopied, type, true);
          else this.isCopied[type] = true;
          setTimeout(() => {
            this.isCopied[type] = false;
          }, 3000);
        }
      },
      /**
       * Save file to Cloud
       */
      async saveToCloud(app_name, index) {
        this.cloudErrorMessage = "";
        // Enable toaster message while user trigger cloud app
        this.toaster = true;
        /**
         * Disable cloud apps
         * @param {Boolean} flag App is in inactive case
         * @param {Boolean} Index App index
         */
        this.disableCloudApps(true, index);
        this.$set(this.isCloudUploading, app_name, true);
        // Check popup window state
        this.checkPopupState(app_name);
        let fileName = `${this.fileData.name}.${this.extension[this.fileType]}`;
        let response = await Upload.initUpload(app_name, this.fileData.url, fileName);
        // eslint-disable-next-line no-prototype-builtins
        response.hasOwnProperty("error") ? (this.cloudErrorMessage = response) : (this.cloudURL[app_name] = response);
        // this.cloudURL[app_name] = await Upload.initUpload(app_name, this.fileData.url, fileName);
        await this.copyLink(this.cloudURL[app_name], app_name);
        this.isCloudUploading[app_name] = false;
        // Disable toaster message while user trigger cloud app
        this.toaster = false;

        /**
         * Enable cloud apps
         * @param {Boolean} flag App is in active case
         */
        this.disableCloudApps(false);
      },

      /**
       * Hide loader & toaster message
       * @param {String} app Active app
       */
      checkPopupState(app) {
        var timer = setInterval(() => {
          if (window.oauthPopup) {
            clearInterval(timer);
            // Hide Loader
            this.isCloudUploading[app] = false;
            this.toaster = false;
            /**
             * Enable cloud apps
             * @param {Boolean} flag App is in active case
             */
            this.disableCloudApps(false);
          }
        }, 1000);
      },

      /**
       * Disable event based on cloud active app
       * @param {Boolean} flag App active or not
       * @param {Number} index App index
       */
      disableCloudApps(flag, index) {
        this.cloudApps.forEach((item, id) => {
          if (flag) {
            if (id !== index) this.$refs.apps[id].style.pointerEvents = "none";
          } else this.$refs.apps[id].style.pointerEvents = "visible";
        });
      },
      /**
       * Get App Logo
       */
      getLogo(app) {
        app = app.toLowerCase().replace(" ", "-");
        return require(`@/assets/ninjacapture/${app}.svg`);
      },
      /**
       * Get Comments
       */
      getComments() {
        NinjaCapture.getComments(this.id).then((response) => {
          this.comments = response;
        });
      },
      /**
       * Add Comment
       */
      addComment() {
        if (this.comment.length === 0 || this.name.length === 0) {
          if (this.comment.length === 0) this.isError.comment = true;
          if (this.name.length === 0) this.isError.name = true;
          return;
        }
        let form = {
          name: this.name,
          comment: this.comment,
        };
        NinjaCapture.addComment(form, this.id).then(() => {
          this.getComments();
          this.hideComment();
        });
      },
      /**
       * Hide comment input
       */
      hideComment() {
        this.showComment = false;
        this.comment = "";
        this.name = "";
        this.isError = {
          name: false,
          comment: false,
        };
      },
      /**
       * Check enter password is valid or not
       */
      checkPassword() {
        if (this.password.length === 0) {
          this.errorMessage = "Password is required";
          return;
        }
        NinjaCapture.checkPassword(this.id, this.password)
          .then(() => {
            this.isDataLoaded = true;
            this.$refs.modal.hide();
            this.password = "";
            this.errorMessage = "";
          })
          .catch(() => {
            this.errorMessage = "Your password did not match. Please try again.";
          });
      },
    },
  };
</script>
<style lang="css" scoped>
  @import url("/css/ninjacapture/ninjacapture.css");
</style>
